.profile{
    width: 400px;
    background-color: rgba(209, 211, 212, 0.685);
    padding-top: 50px;
    padding-bottom: 50px;
    margin-bottom: 10px;
}

.description{
    width: 300px;
    background-color: #fff;
    margin: auto;
    text-align: center;
    padding-top: 20px;
    border-radius: 10px;
}

.avatar{
    border-radius: 50%;
}

.name{
    font-weight: bold;
}
.stats{
    list-style: none;
    background-color: rgba(255, 255, 255, 0.548);
    padding: 0;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    background-color: rgb(238, 232, 232);

}

.stats li{
    padding: 10px;
    border-right-style: solid;
    border-top-style: solid;
    border-width: 1px;
    border-color: rgb(211, 208, 208);

    width: 33%;
}
.stats li:last-child{
    border-right-style: none;
}


.stats span{
    display: block;
}
