.list{
    list-style: none;
}

.item{
    display: flex;
    width: 400px;
    box-shadow: 0 5px 15px rgba(0,0,0,0.25), 0 5px 5px rgba(0,0,0,0.22);
    margin-bottom: 10px;
    padding: 10px;
    align-items: center;
}

.status{
    width: 15px;
    height: 15px;
    border-radius: 50%;
}

.online{
   composes: status;
   background-color: rgb(127, 175, 127);
}

.offline{
 composes: status;
   background-color: rgb(211, 88, 88);
}

.avatar{
    margin-left: 10px;
   border-radius: 10px;
}

.name{
    margin-left: 10px;
    font-size: 22px;
}